.containerCalendario {
    font-family: Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    width: 47vw;
    float: left;
}

.containerSala {
    padding: 20px;
}

.containerBigCalendar {
    padding: 20px;
    height: 115vh
}

.containerFormulario {
    float: right;
    width: 47vw;
}

.blur {
    filter: blur(5px);
}

.containerProgresso {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 30vw;
    height: 30vh;
    z-index: 200;
    padding: 20px;
}

.overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    z-index: 180;
    font-family: Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}